import qiniuUploader from '../../../common/qiniuUploader.js'
export default {
	
	data() {
		return {
			nickname:'',
			avatar:'',
			email:'',
			achievement:''
			
		}
	},
	onLoad() {
		if (!uni.getStorageSync("token") && !uni.getStorageSync("SecretKey")) {
			this.$base1._isLogin()
		}
		this.getUserInfo()
	
	},
	methods: {
		changeAvatar(){
			//修改用户头像
			//获取七牛上传token
			uni.request({
				url: this.baseUrl + "/qiniu-upload",
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					if (res.data.status == 1) {
						//获取七牛返回的token
						this.token = res.data.data.token
						//选择头像图片获取临时地址
						let _self = this
						uni.chooseImage({
							count: 1,
							sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
							sourceType: ['album', 'camera'], //从相册选择，或打开照相机
							success: function(res) {
								console.log('res1', res);
								//图片临时地址
								qiniuUploader.upload(res.tempFilePaths[0], (success) => {
									console.log('===========' + success.imageURL);
									uni.request({
										url: _self.baseUrl + '/member-modify-avatar',
										method: 'POST',
										header: {
											//除注册登录外其他的请求都携带用户token和秘钥
											Authorization: uni.getStorageSync('token'),
											SecretKey: uni.getStorageSync('SecretKey')
										},
										data: {
											Avatar: success.imageURL
										},
										success: res => {
											console.log(res.data);
											uni.showToast({
												title:res.data.message,
												icon:"none"
											})
											_self.getUserInfo()
											
										},
										fail: () => {},
										complete: () => {}
									});
								}, (error) => {
									console.log(error);
								}, {
									region: 'SCN',
									key: new Date().getTime().toString(),
									uptoken: _self.token
								}, (res) => {
									console.log('上传进度', res.progress);
								})
							},
							error: (e) => {
								console.log(e);
							}
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		jumpToSetNickname(){
			//修改用户昵称
			uni.navigateTo({
				url:"./set-nickname"
			})
		},
		getUserInfo(){
			//获取用户信息
			uni.request({
				url: this.baseUrl + "/member-info",
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {
						this.nickname = res.data.data.NickName
						this.avatar = res.data.data.Avatar
						this.email = res.data.data.Email
						this.achievement = res.data.data.Achievement
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		}
		
	}
}
